.query-console-page{
    // background: #fff;
    // margin: 2rem;
    // padding: 2rem;
    
    // min-height: 34rem;
    // height: 100%;
    &__Col{
        // min-height: 19rem;
    }
    &__inner{
        // margin:2rem;
        // padding: 1rem;
    }
    &__clear{
       
        button{
            margin: 0.5rem;
        }
    }
    &__run{
        
        text-align: right;
        button{
            margin: 0.5rem;
        }
    }
    &__iconheader{
        border: 1px solid #f3f3f3;
        background: #f3f3f3;
    }
    &__icon{
        font-size: 1rem;
        padding: 1rem;
    }
    &__jsoncontent{
        border: 1px solid #f3f3f3;
        padding: 1rem;
        max-height: 27rem;
        overflow-y: scroll;
        transition:all 0.5s ease-in;
    }
    // &__img{
    //     width:1.5rem;
    //     height: 1.5rem;
    //     opacity: 0.5;
    //     cursor: pointer;
    // }
    &__responseHeader{
        clear: both;
        display: inline-block;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        padding-top: 1rem;
        padding-left: 1rem;
    }
    &__beautifier{
        color:#1890ff;
    }
    &__history{
        max-height: 16rem;
        overflow-y:scroll;
        &_header{
            h3{
                margin: 0;
                padding: 1rem 0 0 1rem;
            }
        }
    }


    .fadeInright{
        // -webkit-animation-name: fadeInRight;
        // animation-name: fadeInRight;
        animation-delay: 5s;
        animation-name: fadeInRight;
        // animation: fadeInRight 3s fill-mode;
    }
    @keyframes fadeInRight {
        0% {
           opacity: 0;
           transform: translateX(20px);
           transform: translate3d(100%, 0, 0);
        }
        // 50%{
        //     opacity: 0.5;
        //     transform: translateX(60px);

        // }
        100% {
           opacity: 1;
           transform: translateX(0);
           transform: none;
        }
     }
     @media all and (max-width:768px){
         .query-console-page{
             &__icon{
                padding: 0.5rem;
             }
            }
         
     }
}